import './FabricanteApi.css'

import { IonModal, IonToggle } from '@ionic/react'
import { observer } from 'mobx-react-lite'
import React, { useState } from 'react'
import * as FIcon from 'react-feather'
import { RouteComponentProps } from 'react-router'

import { Page } from '../../components'
import { SubHeader } from '../../components/SubHeader'
import { RUTAS_APP_FABRICANTE } from '../../constants'
import { useApiToaster, useToaster } from '../../helpers-ts'
import { useStores } from '../../models/root-store/root-store-context'

const numeral = require('numeral')

export const FabricanteApi: React.FC<RouteComponentProps> = observer(({ history }) => {
  const rootStore = useStores()
  const [Loading, setLoading] = useState(false)
  const Movil = window.innerWidth <= 500 || /Mobi/.test(navigator.userAgent)
  const [ModalApi, setModalApi] = useState(false)
  const [Datos, setDatos] = useState({} as any)
  const [toggle, setToggle] = useState(false)
  const addToast = useToaster()
  const apiToast = useApiToaster()

  const setup = async () => {
    getApiInfo()
  }

  React.useEffect(() => {
    setup()
  }, [])

  const getApiInfo = () => {
    rootStore.environment.api.verApi().then((result: any) => {
      if (result.kind === 'ok') {
        setDatos(result.data)
        setToggle(result.data.api_enabled)
      } else {
        addToast('Ocurrió un error al conectarse al servidor, vuelva a intentar en unos momentos', 'error')
        history.goBack()
      }
    })
  }

    React.useEffect(() => {
        setDato('api_enabled', toggle)
    }, [Datos.api_enabled])

  const toggleApiEnabled = (evt: any) => {
    setToggle(!toggle)
    if (toggle == true) {
       setDato('api_enabled', false)
    }
    if (toggle == false) {
      regenerarApikey()
      setDato('api_enabled', true)
    }
  }

  const setDato = (campo: string, valor: any) => {
    if (Datos) {
      let datos = { ...Datos }
      datos[campo] = valor
      setDatos(datos)
    }
  }

  const guardar = () => {
    setDato('api_enabled', toggle)
    setLoading(true)
    rootStore.environment.api.accionApi('save', Datos).then((result: any) => {
      setLoading(false)
      if (result.kind === 'ok') {
        addToast('Cambios guardados!', 'success')
        setModalApi(false)
      } else if (result.kind === 'unauthorized') {
        addToast('No posee permisos para realizar cambios de configuración de la API', 'warning')
      } else {
        addToast('Ocurrió un error al conectarse al servidor, vuelva a intentar en unos momentos', 'error')
      }
    })
  }

  const regenerarApikey = () => {
    setLoading(true)
    rootStore.environment.api.accionApi('regen-apikey', Datos).then((result: any) => {
      setLoading(false)
      apiToast(result, 'Token regenerado', 'regenerar apikey')
      if (result.kind === "ok") {
        setDato('api_key', result.response)
      }
    })
  }

  return (
    <Page login_required fabricante={true} disableScroll={true} id={'configuracion-fabricante'}>
      <SubHeader noContainer links={[{ titulo: 'Configuración', ruta: RUTAS_APP_FABRICANTE.CONFIGURACION }, { titulo: 'API' }]} />

      <IonModal isOpen={ModalApi} id={'modal-api'} swipeToClose={false} backdropDismiss={false}>
        <>
          <div className="heather-info-registro background-color-fabricante">
            <div>
              <span>Acceso de API de Proveedor</span>
            </div>

            <div onClick={() => setModalApi(false)}>
              <FIcon.XCircle style={{ width: 26, marginRight: 10, color: '#fff' }} />
            </div>
          </div>
          <div className="sub-titulo-login-abi" style={{ padding: '1rem', flexGrow: 1 }}>
            <div className={'form-group'}>
              <div className={'form-group'}>
                <div style={{ display: 'flex', alignItems: 'center' }}>
                  <label id={'api_enabled'} className="mb-0">
                    API habilitada
                  </label>
                  <IonToggle color="dark" checked={toggle} onClick={toggleApiEnabled}></IonToggle>
                </div>
              </div>

              <div className={'form-group'}>
                <label id={'api_key'}>Token de acceso</label>
                <div style={{ display: 'flex' }}>
                  <input
                    type={'text'}
                    readOnly={true}
                    className={'form-control'}
                    id={'api_key'}
                    value={Datos.api_key}
                  />
                  {/*<button className="btn btn-secondary ml-2" onClick={regenerarApikey} disabled={Loading}>*/}
                  {/*  Regenerar*/}
                  {/*</button>*/}
                </div>
              </div>
              <div className={'form-group'}>
                <label id={'ip_safelist'}>Lista de IP's habilitadas para el acceso (separadas entre comas)</label>
                <textarea
                  rows={3}
                  cols={5}
                  className={'form-control'}
                  id={'ip_safelist'}
                  value={Datos.ip_safelist}
                  onChange={(evt) => setDato('ip_safelist', evt.target.value)}
                />
              </div>
              <div className={'form-group'}>
                <label id={'pedidos_signal_endpoint'}>URL de notificaciones de nuevo pedido</label>
                <input
                  type={'url'}
                  className={'form-control'}
                  id={'pedidos_signal_endpoint'}
                  value={Datos.pedidos_signal_endpoint}
                  onChange={(evt) => setDato('pedidos_signal_endpoint', evt.target.value)}
                />
              </div>
              <button className="btn btn-primary px-4" onClick={guardar} disabled={Loading}>
                Acceder
              </button>
            </div>
          </div>
        </>
      </IonModal>

      <div className={'container-lg py-3'}>
        <h3 className="titulo text-center text-lg-left">Documentación de API para Proveedores</h3>
        <div className="subtitulo">¡Bienvenido a la documentación de la API de ABi para Proveedores!</div>
        <p>
          Para ver la documentación de la API puede acceder{' '}
          <a href="https://abi.com.py/api/apidoc.html" target={'_blank'}>
            aquí
          </a>
          .
        </p>
        <p>
          Una vez que tenga una idea del funcionamiento general de la API, puede utilizar su token de acceso y{' '}
          <a href="https://www.postman.com/product/rest-client/" target={'_blank'}>
            POSTMAN
          </a>{' '}
          para realizar pruebas.
        </p>
        <p>
          Para realizar las pruebas, configure su token en el siguiente{' '}
          <a href="https://abi.com.py/api/ABIProveedores.postman_environment.json" target={'_blank'}>
            ambiente de postman</a> y utilice la siguiente{' '}
          <a href="https://abi.com.py/api/ABIProveedores.postman_collection.json" target={'_blank'}>
            colección
          </a>{' '}
          para probar las diferentes llamadas de la API.
        </p>
        <div className="my-3 text-center text-lg-left">
          <button className="btn btn-primary" onClick={() => setModalApi(true)}>
            Acceso API
          </button>
        </div>
      </div>
    </Page>
  )
})

export default FabricanteApi
